import { useEffect, useState } from 'react';

interface Score {
  id: number;
  score: number;
  timestamp: string;
  user: {
    id: number;
    username: string;
  };
}

const RankingPage = () => {
  const [ranking, setRanking] = useState<Score[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  // Obtener el ranking desde la API
  useEffect(() => {
    const fetchRanking = async () => {
      try {
        const response = await fetch('/ranking'); // Endpoint de la API
        if (!response.ok) {
          throw new Error('Error al obtener el ranking');
        }
        const data: Score[] = await response.json();
        setRanking(data);
      } catch (error) {
        if (error instanceof Error) {
          setError(error.message); // Maneja el error correctamente
        } else {
          setError('Error desconocido');
        }
      } finally {
        setLoading(false);
      }
    };

    fetchRanking();
  }, []);

  if (loading) {
    return <div style={styles.loading}>Cargando ranking...</div>;
  }

  if (error) {
    return <div style={styles.error}>Error: {error}</div>;
  }

  return (
    <div style={styles.container}>
      <h1 style={styles.title}>Ranking de Usuarios</h1>
      <table style={styles.table}>
        <thead>
          <tr>
            <th style={styles.th}>Posición</th>
            <th style={styles.th}>Usuario</th>
            <th style={styles.th}>Puntaje</th>
            <th style={styles.th}>Fecha</th>
          </tr>
        </thead>
        <tbody>
          {ranking.map((score, index) => (
            <tr key={score.id} style={styles.tr}>
              <td style={styles.td}>{index + 1}</td>
              <td style={styles.td}>{score.user.username}</td>
              <td style={styles.td}>{score.score}</td>
              <td style={styles.td}>{new Date(score.timestamp).toLocaleDateString()}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default RankingPage;

// Estilos en línea (tipos CSS corregidos)
import { CSSProperties } from 'react';

const styles: { [key: string]: CSSProperties } = {
  container: {
    display: 'flex',
    flexDirection: 'column',  // Valor correcto para flexDirection
    alignItems: 'center',
    justifyContent: 'center',
    padding: '20px',
    fontFamily: 'Arial, sans-serif',
  },
  title: {
    fontSize: '2rem',
    marginBottom: '20px',
    color: '#333',
  },
  table: {
    width: '80%',
    borderCollapse: 'collapse',
    boxShadow: '0 0 10px rgba(0, 0, 0, 0.1)',
  },
  th: {
    backgroundColor: '#0070f3',
    color: '#fff',
    padding: '10px',
    textAlign: 'left',
  },
  tr: {
    borderBottom: '1px solid #ddd',
  },
  td: {
    padding: '10px',
    textAlign: 'left',
  },
  loading: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    fontSize: '1.5rem',
  },
  error: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    fontSize: '1.5rem',
    color: 'red',
  },
};
